.inpus-continer{
    height: 400px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    gap: 10px;

}
.inpus-continer > input{
    border: none;
    border-radius: 3px;
    width: 80%;
    height: 20px;
    position: relative;
}
.inpus-continer > button {
    border: none;
    height: 30px;
    border-radius: 9px;
    background: blue;
    width: 80%;
    margin-top: 30px;
    cursor: pointer;
}
.inpus-continer > button:hover {
    background: rgba(0, 0, 0, 0.5);
}
.inpus-continer> a{ 
    text-decoration: none;
    color: #000000;
}
input:after  {
    content: "يجب ملأ الخانة";
    /* أي أنه يجب أن يكون موضع العنصر في موضع نسبي ليعمل الـ :after بشكل صحيح */
    position: absolute;
    top: 100%; /* قم بتحديد الارتفاع الذي يظهر فيه النص */
    left: 0;
    color: red; /* يمكنك تغيير لون النص حسب احتياجك */
  }
  
@media screen and (min-device-width: 300px) and (max-device-width: 768px) {
    .inpus-continer{
        font-size: 50px;
        height: 900px;
        width: 90%;
    }
    .inpus-continer  > input {
      height: 50px;
      border-radius: 10px;
      margin: 15px;
      width: 80%;
      font-size: 50px;
    }
    .inpus-continer  > button {
      height: 60px;
      width: 80%;
      border-radius: 15px;
      margin-top: 50px;
      color: azure;
      font-size: 30px;
      font-family: "Changa";
  }
  .alert{
    font-size: 40px;
  }
  }
  
